@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color:transparent;
  padding: 0;

}


.Calculator_main__mEdpR {
  width: 100%;
  margin: auto;
  height: 100%;
  border: 1px solid #04ff4a;
  stroke: #04ff4a;
  border-radius: 10px;
  margin-top: 50px;
  background-color: aliceblue,0.6;
  color: #000000;

}

.Calculator_actions__3pZK1 {
  display: flex;
  justify-content: space-between;
  width: 300px;
  margin: auto;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.Calculator_heading__1voKY {
  width: 100px;
  font-size: 18px;
  color: #85bc2d;
}

.Calculator_actions_btn__3lYKh {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  align-items: center;
  background-color: #d2f290;
  height: 40px;
  padding: 15px;
  border-radius: 10px;
  font-size: 18px;
}
.Calculator_actions_input__QGivH {
  border-radius: 50%;
  box-shadow: 2px 2px 2px rgb(142, 139, 140) inset;
  background-color: #d2f290;
  width: 30px;
  text-align: center;
  color: black;
}
button {
  height: 25px;
  background-color: transparent;
  border: none;
  font-size: 18px;
  text-align: center;
  width: 25px;
  cursor: pointer;
}
.Calculator_input_box__1JwgE {
  width: 300px;
  margin: auto;
}

input[type="range"]::-webkit-slider-runnable-track {
  background: #85bc2d;
  border-radius: 10px;
  height: 20px;
}

input[type="range"] {
  -webkit-appearance: none;
  margin-right: 15px;
  width: 300px;
  height: 7px;
  border-radius: 5px;
  background-size: 70% 100%;
  background-repeat: no-repeat;
  margin-top: 50px;
}

/* Input Thumb */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: #acdf46;
  border: 5px solid #d2f290;
  margin-top: -10px;
  cursor: pointer;
}

.Calculator_input_text__1T3lY {
  display: flex;
  justify-content: space-between;
  width: 300px;
  margin-top: 10px;
}
span {
  font-size: 14px;
  color: #000000;
}

.Calculator_insight_box__fg2lv {
  width: 350px;
  margin: auto;
  margin-top: 20px;
  border-radius: 10px;
  border: 1px solid #85bc2d;
  padding: 10px;
  padding-bottom: 0px;
}
.Calculator_data_box__yy2tI {
  display: flex;
  justify-content: space-between;
}
.Calculator_sub_heading__2Rcu3 {
  color: #000000;
  font-size: 16px;
  margin-top: 0px;
}

.Calculator_sub_heading__2Rcu3 > p {
  font-size: 14px;
}

